

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import Logo from '../../../Structure/CommonComponents/Logo';
import { colors } from '../../../../styles/variables'

import EditComponent from './EditComponent'
import DefaultCompoent from './DefaultComponent'


/* Styled Compoents */
const HeaderBox = styled.div`
    height: 4rem;
    background-color: ${colors.white};

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    box-shadow: 0 0 5px rgba(0,0,0,.05);
    /* border-bottom: 1px solid ${colors.gray_line}; */

    display: flex;
    flex-flow: row nowrap;
`; 


/* react component */
class ManageHeader extends Component {

    render () {

        const { 
            isEdit,
            isNew,
            id,
            editCategory,

            title,
            files,
            deletedFileIDs,
            description,
            editSuccess,

            edit_spanStyle,
            edit_textAlign,

            selectedDate,
            roomInfoData,

            /* Methods */
            EditPostRequest,
            selectEditCategory,
            
            editClickB,
            editClickI,
            editClickU,
            editClickS,
            editSelectTextAlign,

            postCarpoolEvent,
        } = this.props;

        return (
            <HeaderBox>
                <Logo isWhite={false} />
                { 
                    isEdit ?

                    <EditComponent 
                        isNew={isNew}
                        id={id}
                        editCategory={editCategory}

                        title={title}
                        files={files}
                        deletedFileIDs={deletedFileIDs}
                        description={description}
                        editSuccess={editSuccess}

                        spanStyle={edit_spanStyle}
                        textAlign={edit_textAlign}

                        selectedDate={selectedDate}
                        roomInfoData={roomInfoData}

                        /* Methods */
                        EditPostRequest={EditPostRequest}
                        selectEditCategory={selectEditCategory}

                        editClickB={editClickB}
                        editClickI={editClickI}
                        editClickU={editClickU}
                        editClickS={editClickS}
                        selectTextAlign={editSelectTextAlign}

                        postCarpoolEvent={postCarpoolEvent}
                    /> :

                    <DefaultCompoent />
                }
                {/* , ,  */}
            </HeaderBox>
        )
    }
}

ManageHeader.propTypes = {
    isEdit: PropTypes.bool,                 // Edit header 

    /* Edit Header  props */
    isNew: PropTypes.bool,                  // Create / Update 
    editCategory: PropTypes.string,         //     
    id: PropTypes.number,                   // Update    id

    title: PropTypes.string,                // Title Data
    files: PropTypes.array,                 // Files Data
    deletedFileIDs: PropTypes.array,        //  ,   id 
    description: PropTypes.string,          // Description Data
    editSuccess: PropTypes.bool,            //    

    edit_spanStyle: PropTypes.object,       // BIUS   
    edit_textAlign: PropTypes.string,       // p text align 

    selectedDate: PropTypes.object,         // Carpool    
    roomInfoData: PropTypes.object,         // Carpool  

    /* Edit Header  Methods */
    EditPostRequest: PropTypes.func,        // Post Create / Update function
    selectEditCategory: PropTypes.func,     //   

    editClickB: PropTypes.func,
    editClickI: PropTypes.func,
    editClickU: PropTypes.func,
    editClickS: PropTypes.func,
    editSelectTextAlign: PropTypes.func,    // Text Align   

    postCarpoolEvent: PropTypes.func,       // Carpool Event  Post Action
}

ManageHeader.defaultProps = {
    isEdit: false,

    /* Edit Header  props */
    isNew: true,
    defaultBoard: '',
    id: 0,
    board: '',

    title: '',            
    files: [],          
    deletedFileIDs: [],   
    description: '',
    editSuccess: false,

    edit_spanStyle: {},
    edit_textAlign: '',

    EditPostRequest: undefined,
    selectEditCategory: undefined,
    editSelectTextAlign: undefined,
}

export default ManageHeader;