//  : pages/manage.js


import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

/* Components */
import {
    Header, Body,
} from '../../components/Manage/ManageStructure'

/* Actions */
import {
    getMyPostRequest,               // Posts GET request 
    deletePostRequest,              // Post Delete Post request   
    updatePostConditionRequest,
} from '../../store/actions/manage'
import { getStatusRequest } from '../../store/actions/auth'

/* Constants */
import {
    MANAGE_POSTS_MARKET,
    MANAGE_POSTS_NETWORKING,
    MANAGE_COMMENTS,
    MANAGE_LIKEPOSTS,
    MANAGE_MYCARPOOL,
    MANAGE_NOTIFICATIONS,
} from '../../constants/manage_category'
import {
    MARKET,
    NETWORKING,
} from '../../constants/categories'


class ManageContainer extends Component {

    state = { isLoad: false }

    async componentDidMount() {
        try {
            const {
                kind
            } = this.props
    
            let board;

            switch(kind) {
    
                /*   */
                case MANAGE_POSTS_MARKET:
                    board = MARKET;
                    // eslint-disable-next-line
                case MANAGE_POSTS_NETWORKING:
                    board = board ? MARKET : NETWORKING;
    
                    // console.log(`posts/${board}`);
                    await this.props.getMyPostRequest(board);
                    await this.props.getStatusRequest();
                    break;
    
                /*   */
                case MANAGE_COMMENTS:
                    // console.log('comment category');
                    break;
    
                /*    */
                case MANAGE_LIKEPOSTS:
                    // console.log('likeposts category');
                    break;
    
                /*    */
                case MANAGE_MYCARPOOL:
                    // console.log('mycarpool category');
                    break;   
                    
                /*    */
                case MANAGE_NOTIFICATIONS:
                    // console.log('notification category');
                    break;
                
                /* kind post  */
                default:
                    break;
            }

            /* window size  ,   state  */
            window.addEventListener('resize', this._updateWindowSize)

            this.setState({
                ...this.state,
                isLoad: true,
                windowHeight: window.innerHeight
            })
        }
        catch {
            this.setState({
                ...this.state,
                isLoad: false
            })
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowSize)
    }

    /* window  update */
    _updateWindowSize = () => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight
        })
    }


    render() {

        const {
            // isGetSuccess,
            user,
            kind,

            postList,

            deletePostRequest,
            updatePostConditionRequest,
        } = this.props;
    
        const { 
            isLoad,
            windowHeight
        } = this.state;

        return(
            <div>
            {
                isLoad ?
                
                    user ? 
                    <div>
                        <Header 
                            isEdit={false} 
                            user={user}
                        />
                        <Body 
                            user={user}
                            kind={kind}
                            windowHeight={windowHeight}

                            postList={postList}
                            deletePost={deletePostRequest}
                            updatePostCondition={updatePostConditionRequest}
                        /> 
                    </div> :
                    <Redirect to='/' />:

                ' '
            }
            </div> 
        )
    }

}

ManageContainer.propTypes = {
    kind: PropTypes.string,
}

const mapStateToProps = (state) => {
    return {
        isGetSuccess: state.manage.isGetSuccess,    // GET   
        err: state.manage.err,                      // GET   err 

        user: state.auth.user,                      //  

        postList: state.manage.postList,            // Posts GET    post list
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => {dispatch(getStatusRequest())},                         //     

        getMyPostRequest: (board) => {dispatch(getMyPostRequest(board))},               //    Post  GET request 
        deletePostRequest: (board, id) => {dispatch(deletePostRequest(board, id))},     // Post Delete POST request 
        updatePostConditionRequest: (board, id, condition) => {                         //    POST request 
            dispatch(updatePostConditionRequest(board, id, condition))
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ManageContainer);