

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Popover } from "antd";
import { MoreOutlined } from "@ant-design/icons";

import { colors } from "../../../styles/variables";
import './ant-popover.css'


/* Styled Components */
/*    */
const MoreIcon = styled(MoreOutlined)`
    font-size: ${props => `${props.size}px`};

    color: ${colors.font_gray};

    :hover {
        color: ${colors.font_darkgray};
    }
`;

/* Content List  Item  */
const PopoverListItem = styled.div`
    padding: 0.375rem 1rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    cursor: pointer;

    :hover {
        background-color: ${colors.gray_bg};
    }
`;

    /* Content Item Icon  */
    const PopoverListItemIcon = styled.div`
        margin-right: 0.5rem;
    `;



/* React Component */
class MoreButton extends Component {

    state = { popoverVisible: false }

    /* Ant-Design Popover visible   */
    _handleVisibleChange = popoverVisible => {
        this.setState({ popoverVisible });
    };

    /*  content item    , popover   */
    _handleContentClick = (clickMethod) => {
        clickMethod();
        this.setState({
            popoverVisible: false,
        });
    }

    /* Content   list react component render  */
    _renderPopoverList = (contentList) => {
        return contentList.map( (contentData, idx) => {
            
            const { text, icon, clickMethod } = contentData

            return (
                <PopoverListItem onClick={() => this._handleContentClick(clickMethod)} key={idx} >
                    {
                        icon ?
                        <PopoverListItemIcon>{icon}</PopoverListItemIcon> :
                        ''
                    }
                    {text}
                </PopoverListItem>
            )
        });
    }


    render() {

        const { popoverVisible } = this.state;

        const {
            size, customContent, contentList
        } = this.props;

        const content = customContent ? customContent : this._renderPopoverList(contentList);

        return (
            <Popover 
                content={content} 
                trigger='click' 
                title={false} 
                placement='bottomRight' 
                visible={popoverVisible}
                onVisibleChange={this._handleVisibleChange}    
            >
                <MoreIcon size={size} />
            </Popover>
        )
    }
}


MoreButton.propTypes = {
    size: PropTypes.number,                     //   
    contentList: PropTypes.array,               /*    Array .  
                                                    elem text handleClick    , 
                                                   icon     . */ 
    customContent: PropTypes.node,              // List  Custom Content .
}

MoreButton.defaultProps = {
    size: 16,
}

export default MoreButton;